import React from "react";
import ReactDOM from "react-dom";
import './leaveComment.css';
import TestimonialsArray from './Testimonials';

function LeaveComment() {

  function leaveComment(){
    document.querySelector(".comment-input").value = "";
    document.querySelector(".comment-textarea").value = "";
    document.querySelector(".LeaveComment").style.display = "none"
  }
  function closeComment(){
    document.querySelector(".comment-input").value = "";
    document.querySelector(".comment-textarea").value = "";
    document.querySelector(".LeaveComment").style.display = "none"
  }

  return(
    <div className="LeaveComment">
      <div className="close" onClick={(e) => {closeComment()}}>&#10006;</div>
      <div className="comment-container">
        <h1>Ваши отзывы о нашем сервисе очень важны для нас!</h1>
        <input className="comment-input" type="text" placeholder="Ваш ник"/>
        <textarea className="comment-textarea" placeholder="Ваш комментарий"/>
        <button className="comment-button" onClick={(e) => {leaveComment()}}>Оставить отзыв</button>
      </div>
    </div>
  )
}

export default LeaveComment;