import './exchange.css'
import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import { Buffer } from 'buffer';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
	useNavigate
} from "react-router-dom";
import './exchange.css'
window.Buffer = Buffer;
let WAValidator = require('multicoin-address-validator');

function Exchange(props){
	const navigate = useNavigate();
	let data = props.props;
	let myStorage = window.localStorage;

	const [currencySell, setCurrencySell] = useState(data.items[0])
	const [currencyBuy, setCurrencyBuy] = useState(data.items[1])
	const [valueIn, setValueIn] = useState("")
	const [valueOut, setValueOut] = useState("")
	const [walletOut, setWalletOut] = useState("")
	const [mail, setMail] = useState("")

	function Capitalize(string){
		return string.charAt(0).toUpperCase() + string.slice(1)
	}

	function currencySelector(e, data){
		let currentList = document.querySelector(`.${e.currentTarget.dataset.list}`);
		let currentListArr = Array.from(currentList.children[1].children);
		currentListArr.forEach((el) => {
			el.classList.remove("active");
		})
		e.currentTarget.classList.add("active");
		switch(e.currentTarget.dataset.list){
		case 'currency-sell':
			setCurrencySell(data)
			let listBuy = Array.from(document.querySelector('#list-buy').children);
			listBuy.forEach((el) => {
				if(el.dataset.number === e.currentTarget.dataset.number){
					el.style.display = "none"
				} else {
					el.style.display = "flex"
				}
			})
			break;
		case 'currency-buy':
			setCurrencyBuy(data)
			let listSell = Array.from(document.querySelector('#list-sell').children);
			listSell.forEach((el) => {
				if(el.dataset.number === e.currentTarget.dataset.number){
					el.style.display = "none"
				} else {
					el.style.display = "flex"
				}
			})
			break;
		}
	}

	function setStartingState(){
		let startingCurrencySell = document.querySelector('[data-list="currency-sell"][data-number="0"]');
		let startingCurrencyBuy = document.querySelector('[data-list="currency-buy"][data-number="1"]');
		let startingCurrencySellHide = document.querySelector('[data-list="currency-sell"][data-number="1"]');
		let startingCurrencyBuyHide = document.querySelector('[data-list="currency-buy"][data-number="0"]');
		startingCurrencySell.classList.add("active");
		startingCurrencyBuy.classList.add("active");
		startingCurrencySellHide.style.display = "none";
		startingCurrencyBuyHide.style.display = "none"; 
	}

	function resetWalletState(){
		setWalletOut("");
	}

	function calculateMinimumRate(item){
		return (50/item.market_data.current_price.usd).toFixed(8)
	}

	function calculateDefaultPrices(itemSell, itemBuy){
		let defaultSellPrice = calculateMinimumRate(itemSell);
		let defaultBuyPrice = calculateMinimumRate(itemBuy);
		setValueIn(defaultSellPrice);
		setValueOut(defaultBuyPrice);
	}

	function calculateOffer(itemSell, itemBuy, target){
		switch(target.name){
			case "valueIn":
				setValueOut(((itemSell.market_data.current_price.usd / itemBuy.market_data.current_price.usd) * target.value.replace(",", ".")).toFixed(8))
			break;
			case "valueOut":
				setValueIn(((itemBuy.market_data.current_price.usd / itemSell.market_data.current_price.usd) * target.value.replace(",", ".")).toFixed(8))
			break;
		}
	}

	function formCheck(formData){
		let valid = WAValidator.validate(formData.wallet, currencyBuy.symbol);

		let allertsWindow = document.querySelector("#allertsWindow"),
				allertSellPrice = document.querySelector("#allertSellPrice"),
				allertBuyPrice = document.querySelector("#allertBuyPrice"),
				allertNoWallet = document.querySelector("#allertNoWallet"),
				allertWalletInvalid = document.querySelector("#allertWalletInvalid");

		let toClear = [allertsWindow,allertSellPrice,allertBuyPrice,allertNoWallet,allertWalletInvalid]
		allertsWindow.style.display = "none";
		toClear.forEach((el) => {el.style.display = "none"});

		if(parseFloat(formData.valueIn) < calculateMinimumRate(currencySell)){
			allertsWindow.style.display = "flex";
			allertSellPrice.style.display = "block";
		}else if(formData.wallet.length < 1){
			allertsWindow.style.display = "flex";
			allertNoWallet.style.display = "block";
		}else if(!valid){
			allertsWindow.style.display = "flex";
			allertWalletInvalid.style.display = "block";
		} else {
			return true
		}
	}

	function createOfferId(){
    if(myStorage.OFFER_ID === undefined){
      myStorage.setItem('OFFER_ID', Math.random().toFixed(8).toString().slice(2))
      myStorage.setItem('OFFER_STATUS', 'Ожидает оплаты');
    } else if(myStorage.OFFER_ID){

    }
  }

  function SubmitForm(e){
    e.preventDefault(); 
    myStorage.clear();
    createOfferId();
    let compiledForm = new FormData(e.target);
    let formData = {
    	currencySell: currencySell.id,
    	valueIn:`${compiledForm.get("valueIn")}`,
    	currencyBuy: currencyBuy.id,
      valueOut:`${compiledForm.get("valueOut")}`,
      wallet:`${compiledForm.get("wallet")}`,
      mail: `${compiledForm.get("mail")}`
    }
    if(formCheck(formData)){
    	navigate('/Offer', {state: formData})
    };
  }


	useEffect(() => {
		setStartingState();
	}, [])

	useEffect(() => {
		calculateDefaultPrices(currencySell, currencyBuy)
	}, [currencySell, currencyBuy]);

	useEffect(() => {
		resetWalletState();
	}, [currencyBuy]);

    return (
    	<div className="Exchange">

    		<div className="Exchange__left-block">

    			<div className="currency-sell">
    				<div className="list-header">
    					<h2 className="title">Отдаете</h2>
    				</div>
    				<ul className="currency-list" id="list-sell">
        			{data.items.map(function(item, i) {
        			  return (
        			  	<li className="list-item" data-list={"currency-sell"} data-number={i} key={i} onClick={(e)=>{currencySelector(e, item)}}>
        			  		<div>
        			  			<img src={item.image.thumb} alt="" />
        			  			<p>{Capitalize(item.id)}</p>
        			  		</div>
        			  		<p>100</p>
        			  	</li>
        			  )
        			})}
      			</ul>
    			</div>

    			<div className="currency-buy">
    				<div className="list-header">
    					<h2 className="title">Получаете</h2>
    				</div>
    				<ul className="currency-list" id="list-buy">
        			{data.items.map(function(item, i) {
        			  return (
        			  	<li className="list-item" data-list={"currency-buy"} data-number={i} key={i} onClick={(e)=>{currencySelector(e, item)}}>
        			  		<div>
        			  			<img src={item.image.thumb} alt="" />
        			  			<p>{Capitalize(item.id)}</p>
        			  		</div>
        			  		<p>100</p>
        			  	</li>
        			  )
        			})}
      			</ul>
    			</div>

    		</div>

    		<div className="Exchange__right-block">
    			<div className="preorder-block">
    				<div className="Exchange-block">
    					<form onSubmit={(e) => {SubmitForm(e)}} name="exchangeForm">

    						<h2 className="title title-remove-padding" >Обмен {Capitalize(currencySell.id)}, {currencySell.symbol.toUpperCase()} на {Capitalize(currencyBuy.id)}, {currencyBuy.symbol.toUpperCase()}</h2>
    						
    						<div className="Exchange-header">
    							<p className="preorder-course" >1 {Capitalize(currencySell.id)} {currencySell.symbol.toUpperCase()} = {Capitalize(currencyBuy.id)} {currencyBuy.symbol.toUpperCase()}</p>
    						</div>
    						<div className="allerts-area" id="allertsWindow">
    							<p className="allert-message" id="allertSellPrice">Сумма продажи должна быть больше чем {calculateMinimumRate(currencySell)}.</p>
    							<p className="allert-message" id="allertBuyPrice">Сумма покупки должна быть больше чем {calculateMinimumRate(currencyBuy)}.</p>
    							<p className="allert-message" id="allertNoWallet">Необходимо заполнить «Номер счета/кошелька».</p>
    							<p className="allert-message" id="allertWalletInvalid">Номер счёта\кошелька не прошёл проверку.</p>
    						</div>
    						<div className="field-area">

    							<div className="sell-block">
    								<div className="currency-header">
    									<img className="currency-img" src={currencySell.image.small} alt="" />
    									<h2 className="title"><span className="hint-block">От {calculateMinimumRate(currencySell)} {currencySell.symbol.toUpperCase()}</span>Отдаю {Capitalize(currencySell.id)}</h2>
    								</div>
    								<input className="form-control" type="text" name="valueIn" value={valueIn} onChange={(e) => {setValueIn(e.target.value); calculateOffer(currencySell, currencyBuy, e.target)}}/>
    							</div>

    							<div className="buy-block">
    								<div className="currency-header">
    									<img className="currency-img" src={currencyBuy.image.small} alt="" />
    									<h2 className="title"><span className="hint-block">От {calculateMinimumRate(currencyBuy)} {currencyBuy.symbol.toUpperCase()}</span>Получаю {Capitalize(currencyBuy.id)}</h2>
    								</div>
    								<input className="form-control" type="text" name="valueOut" value={valueOut} onChange={(e) => {setValueOut(e.target.value); calculateOffer(currencySell, currencyBuy, e.target)}}/>
    								<div className="buy-form-wallet">
    									<label className="buy-wallet-label" htmlFor="wallet">Номер счета/кошелька</label>
    									<input className="form-control" type="text" name="wallet" value={walletOut} onChange={(e) => {setWalletOut(e.target.value)}} />
    								</div>
    								<div className="buy-form-mail">
    									<label className="buy-mail-label" htmlFor="mail">Ваш Email</label>
    									<input className="form-control" required type="email" name="mail" value={mail} onChange={(e) => {setMail(e.target.value)}} />
    								</div>
    							</div>
    						</div>
    						<div className="rules-block">
    							<input className="rules-checkbox" type="checkbox" required name="rules" />
    							<label htmlFor="rules" className="rules-text">Согласен с <Link to="/about">Правилами сайта</Link></label>	
    						</div>
    						<div className="button-block">
    							<button className="form-submit" type="submit">Обменять</button>
    						</div>
    					</form>
    				</div>
    			</div>

    		</div>
    	</div>
    )
  }

export default Exchange