import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import {useLocation} from 'react-router-dom';
import './afterOffer.css'

function AfterOffer(props) {

  const location = useLocation();
  let basicData = props.props;
  let data = location.state.FORWARDED_FORM_DATA;
  let offerId = location.state.offerId;
  useState(() => {
    console.log(location)
  });


  function findSymbol(currency){
    let symbol;
    basicData.items.forEach((el) => {
      if(el.id === currency){
        symbol = el.symbol.toUpperCase()
      }
    });
    return symbol
  }

  return(
    <div className="AfterOffer">
      <div className="container">
        
          
          <div className="text-content">
          <h1>Спасибо что использовали наш сервис!</h1>
          <h4>Ваша заявка <span className="green">#{offerId}</span> была удачно обработана нашим сервисом</h4>
          <div className="data-display">
            <h5>Вы обменяли:</h5>
            <div>
              <h5>{data.valueIn}</h5> 
              <h4>{findSymbol(data.currencySell)}</h4>
            </div>
            <h5>На:</h5>
            <div>
              <h5>{data.valueOut}</h5> 
              <h4>{findSymbol(data.currencyBuy)}</h4>
            </div>
          </div>
          <p>Если вы хотите оставить отзыв о нашем сервисе - сделайте это на главной странице нашего сайта!</p>
          <p>Если у вас возникли каие либо проблемы с работой сервиса - вы всегда можете обратится в поддержку и вам будет оказана помощь в любом вопросе связанном с услугами которые мы предоставляем</p>
          <p>Если обработка заявки заняла слишком долго - сообщите об этом в отзыве! Ваши комментарии очень важны для нас!</p>
        </div>
      </div>
    </div>
  )
}

export default AfterOffer;