import React from "react";
import ReactDOM from "react-dom";
import { Link } from "react-router-dom";
import Ticker from './Ticker'
import LOGO from '../images/LOGO.png'
import './navbar.css'

function Navbar(props) {
  let data = props.props;
  return(
    <nav className="navbar">

      <div className="navbar__row">
        <div className="navbar__logo">
          <Link className="navbar__link" to="/"><img className="navbar-logo" src={LOGO} alt="" /></Link>
        </div>

        <ul className="navbar__links">
          <li>
            <Link className="navbar__link" to="/"><h2>Главная</h2></Link>
          </li>
          <li>
            <Link className="navbar__link" to="/about"><h2>Правила</h2></Link>
          </li>
        </ul>
      </div>
      <ul className="navbar__links-row2">
        <li>
          <div className="navbar__info">
            <h5>Работаем круглосуточно</h5>
            <h5>Тех. поддержка: 9:00 - 23:00</h5>
          </div>
        </li>
        <li>
          <div className="navbar__info">
            <h5>Поддержка по почте: sbexchange@proton.me</h5>
            <h5>Telegram: <a href="https://t.me/sbexchange01" className="tech-support">Тех. поддержка </a></h5>
          </div>
        </li>
      </ul>

      <Ticker props={data}/>
    </nav>
  )
}

export default Navbar;