import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
  useHistory
} from "react-router-dom";

import Navbar from './components/Navbar'
import Home from './components/Home'
import Rules from './components/Rules'
import Footer from './components/Footer'
import Offer from "./components/Offer"
import CoingeckoAPI from "./API/CoingeckoAPI"
import Loading from "./components/Loading"
import AfterOffer from "./components/AfterOffer"
import './App.css';
import Telegram from './images/Telegram.png'

function App() {
  const [state, setState] = useState({
    error: null,
    isLoaded: false,
    items: []
  });

  useEffect(() => {
    getAPIData();
  }, [])

  useEffect(() => {
    if(!state.isLoaded){
      recallTimeout = setTimeout(RECALL, 2000)
    } else {
      clearTimeout(recallTimeout)
    }
  }, [state])

  function getAPIData() {
    CoingeckoAPI()
      .then(data => {
        setState({
          isLoaded: true,
          items: data
        })
      })
      .catch(err => 
        setState({
          isLoaded: false,
          error: err
        })
      )
  };

  let recallTimeout;

  function RECALL(){
    clearTimeout(recallTimeout)
    getAPIData();
  }

  if(!state.isLoaded){
    return(
      <Loading/>
    )
  } else if(state.isLoaded){
      return (
      <div className="App">
        <Navbar props={state}/>
        <section className="App-section">
    
          <Routes>

            <Route path="/AfterOffer" element={<AfterOffer props={state}/>}/>

            <Route path="/Offer" element={<Offer props={state}/>}/>

            <Route path="/about" element={<Rules props={state}/>}/>
    
            <Route path="/" element={<Home props={state}/>}/>
    
          </Routes>
  
        </section>
        <Footer props={state}/>
        <div className="support-block">
          <div className="support-block-caller"></div>
          <a href="https://t.me/sbexchange01"><img className="static-support" src={Telegram} alt="" /></a>
        </div>
        
      </div>
    );
  }
}

export default App;
