import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import {useNavigate, useLocation} from 'react-router-dom';
import {TelegramCall} from '../API/TelegramCall';
import WALLETS from '../API/WALLETS';
import './offer.css'


function Offer(props) {
  const location = useLocation();
  const navigate = useNavigate();
  let data = props.props
  let FORWARDED_FORM_DATA = location.state

  const [mail, setMail] = useState("");
  const [symbol, setSymbol] = useState("");
  const [offerStatus, setOfferStatus] = useState("");
  let myStorage = window.localStorage;

  useEffect(() => {
    findImageForCurrency();
    findSymbol(FORWARDED_FORM_DATA.currencySell) 
    CheckStatus(); 
  }, [])

  useEffect(() => {
    UpdateStatus();
  },[offerStatus])

  function Capitalize(string){
    return string.charAt(0).toUpperCase() + string.slice(1)
  }

  function findImageForCurrency(){
    let imageSell = document.querySelector("#imageSell"),
        imageBuy = document.querySelector("#imageBuy");

    data.items.forEach((el) => {
      if(el.id === FORWARDED_FORM_DATA.currencySell){
        imageSell.src = el.image.small;
      }
      if(el.id === FORWARDED_FORM_DATA.currencyBuy){
        imageBuy.src = el.image.small;
      }
    })
  }

  function findSymbol(currencySell){
    data.items.forEach((el) => {
      if(el.id === currencySell){
        setSymbol(el.symbol.toUpperCase())
      }
    });
  }

  let waitingPaymentTimer;
  let waitingConfirmationTimer;
  let waitingAutoTimer;

  function waitingPayment(){
    setOfferStatus('Отменен (Оплата не совершена)');
    document.querySelector('.paid-button').style.display = "none"
    document.querySelector('#offerStatus').style.color = "red"
  };
  function waitingConfirmation(){
    setOfferStatus('Ожидает подтверждение от системы автовыплат')
    document.querySelector('.paid-button').style.display = "none"
  }
  function waitingAuto(){
    let afterOfferData = {
      offerId: myStorage.OFFER_ID,
      FORWARDED_FORM_DATA
    }
    navigate("/AfterOffer", {state: afterOfferData})
  }

  function CheckStatus(){
    if(myStorage.OFFER_STATUS === 'Ожидает оплаты'){
      setOfferStatus('Ожидает оплаты');
    }else if(myStorage.OFFER_STATUS === 'Ожидает подтверждение'){
      setOfferStatus('Ожидает подтверждение');
      document.querySelector('.paid-button').style.display = "none"
    } else if(myStorage.OFFER_STATUS === 'Ожидает подтверждение от системы автовыплат'){
      setOfferStatus('Ожидает подтверждение от системы автовыплат')
      document.querySelector('.paid-button').style.display = "none"
    }
  }

  function PaidCall(){
    clearTimeout(waitingPaymentTimer)
    myStorage.OFFER_STATUS = 'Ожидает подтверждение'
    setOfferStatus('Ожидает подтверждение')
    document.querySelector('.paid-button').style.display = "none"
    let TGDATA = {
      inCurrency: FORWARDED_FORM_DATA.currencySell,
      input: FORWARDED_FORM_DATA.valueIn,
      outCurrency: FORWARDED_FORM_DATA.currencyBuy,
      output: FORWARDED_FORM_DATA.valueOut,
      wallet: FORWARDED_FORM_DATA.wallet,
      email: FORWARDED_FORM_DATA.mail,
      orderId: myStorage.OFFER_ID
    }
    console.log(TGDATA)
    TelegramCall(TGDATA)
  }


  function UpdateStatus(){
    if(offerStatus.length > 0){
      if(offerStatus === 'Ожидает оплаты'){
        waitingPaymentTimer = setTimeout(waitingPayment, 1800000 )
      } else if(offerStatus === 'Ожидает подтверждение'){
          clearTimeout(waitingPaymentTimer);
          document.querySelector('.paid-button').style.display = "none"
          waitingConfirmationTimer = setTimeout(waitingConfirmation, 300000)
      } else if(offerStatus === 'Ожидает подтверждение от системы автовыплат'){
          waitingAutoTimer = setTimeout(waitingAuto, 300000)
      }
    }
  }










    return(
      <div className="Offer">
        <div className="container offer-container">
          <div className="left_block">
            <p className="page-title">Заявка на обмен #{myStorage.OFFER_ID}</p>
            <div className="order-info">
              <h5>Порядок действий</h5>
              <p>Совершите платеж на сумму <strong>{FORWARDED_FORM_DATA.valueIn} {symbol}</strong> на счёт:</p>
              <p><strong>{WALLETS[FORWARDED_FORM_DATA.currencySell]}</strong></p>
              <p>После оплаты кликните по кнопке «Я ОПЛАТИЛ», чтобы мы получили уведомление и проверили поступление средств.</p>
              <p className="alert-block">Сервис обращает внимание про недопустимость проведения платежей с высокорисковых площадок или средств, полученных ранее с таких сервисов! Каждая операция проходит проверку через независимый complaint-сервис Crystal. При получении результата проверки в 70% и выше такие платежи будут приостановлены и возвращены за вычетом комиссии сети Биткоин! Обмен таких средств невозможен!</p>
              <p>Оплата считается полученной только после получения 1-го подтверждения сети.</p>
              <p className="alert-block-yellow">В случае отсутствия транзакции в мемпуле сети биткоин спустя 20 минут заявка будет отменена. При поступлении средств позже регламента обратитесь в наш онлайн-чат для решения вопроса в ручном режиме.</p>
              <p>Инициация вывода с биржи еще не значит проведение платежа в сети биткоин. Биржи очень часто задерживают выплаты вплоть до нескольких дней. Следите всегда в первую очередь за состоянием адреса биткоина, так как это единственный и абсолютно достоверный показатель совершения перевода.</p>
              <p>При оплате в срок заявка фиксируется на 1 день и выплата будет совершена без изменений курса независимо от колебаний цены на биржах.</p>
              <img className="walletQR" src={`http://api.qrserver.com/v1/create-qr-code/?data=${WALLETS[FORWARDED_FORM_DATA.currencySell]}&size=250x250`} alt="" />
              <p>После подтверждения получения оплаты заявка будет завершена в срок от 3 до 30 минут.</p>
              <p>После выполнения заявки с нашей стороны криптовалюта может отобразиться на Вашем кошельке не сразу. Это полностью зависит от той платформы, на которую Вы совершаете перевод. Иногда для отображения средств на счету транзакция должна получить подтверждения сети.</p>
            </div>
          </div>
          <div className="right_block">

            <div className="form-item">
                <p className="item-title">Статус:</p> <p id="offerStatus" className="green">{offerStatus}</p>
            </div>

            <div className="form-data">
              <div className="offer-currency-header">
              <img className="header-image" id="imageSell" alt=""/>
                <p className="offer-title">Отдаю {Capitalize(FORWARDED_FORM_DATA.currencySell)}</p>
              </div>              
              <div className="form-item">
                <p className="item-title">Вы отдаёте:</p> <p>{FORWARDED_FORM_DATA.valueIn} </p>
              </div>
              <div className="offer-currency-header">
              <img className="header-image" id="imageBuy" alt=""/>
                <p className="offer-title">Получаю {Capitalize(FORWARDED_FORM_DATA.currencyBuy)}</p>
              </div>
              <div className="form-item">
                <p className="item-title">Вы получите:</p> <p>{FORWARDED_FORM_DATA.valueOut} </p>
              </div>
              <div className="form-item">
                <p className="item-title">На счёт:</p> <p> {FORWARDED_FORM_DATA.wallet}</p>
              </div>
              <div className="form-item">
                <p className="item-title">Email:</p> <p> {FORWARDED_FORM_DATA.mail}</p>
              </div>
              <div className="form-item centered">
                <button className="paid-button" onClick={(e) => PaidCall()}>Я оплатил!</button>
              </div>
            </div>

          </div>

        </div>
      </div>
      
    )
}

export default Offer;