import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import axios from 'axios';
import WALLETS from "./WALLETS"

function createApiRoutes(coins){
  let routes = [];
  Object.keys(coins).forEach(el => {
    routes.push(`https://api.coingecko.com/api/v3/coins/${el}?localization=false&community_data=false&developer_data=false&tickers=false`)
  })
  return routes;
}


const CoingeckoAPI = () => {
  let routes = createApiRoutes(WALLETS)

  let promise = axios.all(routes.map((endpoint) => axios.get(endpoint)));
  const dataPromise = promise.then((res)=> {
    let responses = [];
    res.forEach(el => {
      responses.push(el.data)
    })
    return responses;
  })
  return dataPromise
};


export default CoingeckoAPI;