import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import './ticker.css'

function Ticker(props) {
  let data = props.props;

  function CompileTickerElementDataFirstHalf(items){
    let style;
    return items.map((item, i) => {
      if(item.market_data.price_change_percentage_24h >= 0){
        style = "rate-up"
      } else if(item.market_data.price_change_percentage_24h < 0){
        style = "rate-down"
      }
      return(
        <div key={i} className="ticker__element">
          <a href="#"><span className="name">{item.symbol.toUpperCase()}</span>({item.market_data.current_price.usd}$ <span className={style}>{item.market_data.price_change_percentage_24h.toString().slice(0, 5)}%</span>)</a>
        </div>
      )
    })
    
  }
  function CompileTickerElementDataSecondHalf(items){
    let style;
    return items.map((item, i) => {
      if(item.market_data.price_change_percentage_24h > 0){
        style = "rate-up"
      } else if(item.market_data.price_change_percentage_24h <= 0){
        style = "rate-down"
      }
      return(
        <div key={i} className="ticker__element">
          <a href="#"><span className="name">{item.symbol.toUpperCase()}</span>({item.market_data.current_price.usd}$ <span className={style}>{item.market_data.price_change_percentage_24h.toString().slice(0, 5)}%</span>)</a>
        </div>
      )
    })
    
  }

  return(
    <div className="ticker-wrapper">
      <div className="ticker__first-half">
        {
          CompileTickerElementDataFirstHalf(data.items)
        }
      </div>
      <div className="ticker__second-half">
        {
          CompileTickerElementDataSecondHalf(data.items)
        }
      </div>
    </div>
  )
}

export default Ticker;