import React from "react";
import ReactDOM from "react-dom";
import './rules.css'

function Rules() {
  return(
    <div>
      <h1>Правила пользования сервисом SBEXCHANGE.IO</h1>
      <div className="rules-section">
        <h5>Для того, чтобы произвести р2р-операцию с помощью нашего сервиса, Вам необходимо следовать следующим правилам:</h5>
        <p>1. Выберите интересующее Вас направление обмена и нажмите на ссылку <strong>Обменять</strong>.</p>
        <p>2. В открывшейся форме заполните все поля. Во всех полях обязательно указывайте достоверную информацию. В случае несоблюдения этого условия сервис оставляет за собой право отказать в обмене и вернуть оплаченные пользователем деньги за вычетом всех комиссий на операции. При заполнении обратите внимание на строку <strong>Итого</strong>. В ней Вы можете увидеть сумму, которую необходимо оплатить с учетом всех комиссий.</p>
        <p>Прочтите, пожалуйста, разделы Правила и Безопасность, и поставьте галочку о прочтении.</p>
        <p>3. После нажатия на кнопку <strong>Обменять</strong>, Вы увидите Вашу заявку на обмен. Если на данном этапе Вы заметили неточность в реквизитах или сумме - вернитесь на предыдущую страницу и внесите необходимые коррективы в реквизиты заявки на обмен.</p>
        <p>4. Обращаем внимание, что заявка фиксируется по текущему курсу и исполняется также по нему, если оплата поступила в течении 20 минут после создания. Заявка отменяется, если оплата не приходит в указанное время. В случае оплаты после этого срока сервис оставляет за собой право завершить заявку по новому курсу, который согласуется на момент обращения клиента по восстановлению заявки, либо вернуть средства отправителю за вычетом комиссий платежной системы. В случае оплаты криптовалюты со стороны клиента в регламентные сроки временем оплаты является совершение платежа, а не приход 1-го подтверждения.</p>
        <p>5. После этого в зависимости от направления обмена Вы либо будете переадресованы на мерчант платежной системы для оплаты для автоматических и полуавтоматических обменов, либо получите реквизиты, на которые нужно провести оплату для ручных направлений обмена.</p>
        <p>6. После получения оплаты Вы получите запрошенную по обмену валюту. Обычно это занимает от 2-х до 30 минут в рабочее время. В редких случаях обмен может занять до 24-х часов из-за каких-либо технических проблем.</p>
        <p>7. Операции по обмену криптовалют проходят только после получения 1-го подтверждения для Биткоин и 2-го подтверждения для Эфириум</p>
        <p>8. Переводы в системе биткоин могут долго быть в состоянии "Неподтвержденная транзакция". Чаще всего это происходит из-за того, что одна из операций долго не проходит. По правилам работы сети Биткоин такой проблемный перевод крепится к другому блоку и в дальнейшем идет по стандартной процедуре. Если этого не происходит, то в промежуток 1-7 дней средства возвращаются отправителю. Наш обменный сервис, к сожалению, никак не может повлиять на скорость обработки таких переводов. Если такой платеж задержался на продолжительное время, то после его зачисления либо возврата отправителю ситуация с заявкой будет разрешена путем ее завершения либо отмены с возвратом всех денежных обязательств каждой из сторон.</p>
        <p>9. Любые операции обмена могут быть приостановлены для дополнительных проверок Службы Безопасности как нашего сервиса, так и Банков с Платежными системами. Систематическое создание неоплаченных заявок расценивается как вредоносное действие в сторону сервиса. Доступ к услугам сервиса может быть ограничен или запрещен. При оплате через Приложение Приватбанка комментарий получателю невиден. Поэтому при таком способе оплаты могут быть дополнительно запрошены квитанции, скриншоты или подтверждающие переводы. В случае невыполнения дополнительных верификационных процедур, средства будут возвращены отправителю за вычетом возможных комиссий банка. Оплаты через Терминалы / Кассы / IVR-меню категорически не принимаются и будут отклонены.</p>
        <p>10. Сервис в праве запросить верификацию любого пользователя и его платежных данных. Администрация в праве запросить к верификации любые документы, подтверждающие личность, а также фото с документом в руках.</p>
        <p>11. Сервис оставляет за собой право приостановить обменную операцию клиента, если она является высоко рисковой или незаконной, по данным <strong>complaint</strong> партнеров Сервиса. После выполнения верификационных процедур разрешенный возврат рисковых криптоактивов осуществляется согласно регламента сервиса в срок до 24 часов.</p>
        <p>12.В случае указания некорректного адреса для получения крипто активов автоматизированная система не сможет выполнить данное направление по обмену. Для разблокировки данной транзакции необходимо будет создать повторную заявку и оплатить ее на сумму идентичную в замороженной заявке или оформить заявку на возврат. Возврат занимает до 90 рабочих дней.</p>
        <p><strong>ВАЖНО!</strong> Мы нигде <strong>не публикуем наши счета</strong> для принятия средств от клиентов! <strong>Обмен ведется только через сайт!</strong> Не поддавайтесь на уловки мошенников.</p>
        <p>Сервис запрещает обмены на счета 3-х лиц. Мы не несем ответственности за действия этих лиц после получения средств от нашего обменного сервиса. Проводите операции только с кошельками и счетами, к которым имеете полный доступ.</p>
        <p>Если у Вас возникнут вопросы на любом этапе взаимодействия с нашим сервисом - пожалуйста обращайтесь к нашему оператору в чате онлайн помощи. Он ответит на все вопросы и окажет помощь относительно нашего сервиса.</p>
        <h5>График работы сервиса: Круглосуточно. Спасибо за то, что прочитали правила!</h5>
      </div>
    </div>
  )
}

export default Rules;